<template recyclable>
  <div class="card">
      <image class="image"
       :src="card.imgSrc" @click="toggleCase(card)"></image>
       <text>{{card.txt}}</text>
  </div>
</template>

<script>
  export default {
    props: ['card'],
    methods: {
      toggleCase (i) {
        console.error('====rzl toggleCase item:' + JSON.stringify(i))
        i.imgSrc ='https://www.likecs.com/default/index/img?u=aHR0cHM6Ly9waWFuc2hlbi5jb20vaW1hZ2VzLzI2Mi80YTlmZGQ0ZTRhYzcwOGUwOTIyMjFmM2FiYjA5NWZiZS5wbmc='
        i.txt = ''
      }
    }
  }
  
</script>

<style scoped>
  .card {
    width: 650px;
    border-width: 4px;
    border-style: solid;
    border-color: #E8E8E8;
    border-radius: 20px;
    margin: 50px;
  }
  .image {
    width: 650px;
    height: 200px;
  }
  .title {
    text-align: center;
    font-size: 40px;
    color: #888;
    background-color: #F5F5F5;
    padding: 12px;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    border-bottom-color: #E0E0E0;
  }
  .body {
    height: 200px;
    justify-content: center;
  }
  .label {
    text-align: center;
    font-size: 100px;
    color: #1FB5FC;
  }
</style>



