<template>
  <recycle-list for="(item, i) in listData" switch="type">
    <cell-slot case="static">
      <text class="text">-- static --</text>
    </cell-slot>
    <cell-slot case="dynamic">
      <card :index="i" :card="item"></card>
    </cell-slot>
  </recycle-list>
</template>

<script>
  import Card from './Card.vue'
  
  const img1='https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg';
  const img2='https://aecpm.alicdn.com/simba/img/TB14ab1KpXXXXclXFXXSutbFXXX.jpg_q50.jpg';
  
  function generateData (sentence) {
    return sentence.split('').map((type, index) => {
      if (type === '_') {
        return { type: 'static' }
      }
      return {
        type: 'dynamic',
        label: type,
        imgSrc:img1,
        txt: 'sssss',
        height: '100px',
        width:'400px'
      }
    })
  }

  export default {
    components: { Card },
    data () {
      return {

        listData: generateData('_MAY_THE_FORCE_BE_WITH_YOU_')
      }
    },
    methods: {
      // toggleCase (i) {
      //   const item = this.listData[i];
      //   item.imgSrc = ( item.imgSrc=== img1?img2:img1);
      // }
    }
  }
</script>

<style scoped>
  .text {
    text-align: center;
    font-size: 50px;
    padding: 15px;
    color: #606060;
    background-color: #E8E8E8;
  }
</style>



